.homeall{
    overflow:hidden;
}
.search {
    background-color: rgb(250, 142, 70);
    padding-top: 0;
    width:100%;
    height:42px;
    color:#fff;

}
.search button{
    width:325px;
    height: 26px;
    background-color: rgb(248, 87, 24);
    border: none;
    border-radius: 5px;
    margin-top: 8px;
    float: right;
    margin-right: 10px;
    color:#fff;
    font-size:15px;
}
.like{
    text-align: center;
    color: rgb(247, 105, 11);
    padding-top: 10px;
    font-size:16px;
}
.image{
    display: inline-block;
    width:100%;
    padding-top: 20px;
    border:none;
    border-radius: 7px;
    margin-top: 0px;
    padding-left: 10px;
    margin-bottom: 10px;
}
.list{
    background-color: #fff;
    width:170px;
    height:235px;
    margin-top: 0px;
    float:left;
    border:none;
    border-radius: 7px;
    margin-right: 10px;
    margin-bottom: 20px;

}
.list button{
    border:none;
    border-radius: 5px;
    margin-top: 0px;
    padding-top:0;
    padding-left:0;
    width:100%;
    height:225px;
    padding-bottom: 20px;
    background-color: #fff;
}
.list button img{
    border:none;
    width:170px;
    border-radius:5px;
    top: 0;
    float:left;
    position: relative;
    margin-bottom: 5px;

}
.price{
    float:left;
    color:rgb(253, 116, 3);
    font-size: 18px;
    margin-top: 3px;
}
.price span{
    color:rgb(160, 160, 160);
    font-size: 15px;
}
.bot{
    width: 100%;
    height:300px;
    background-color: #fff;
}