#all {
    width:100%;
    overflow:hidden;
    height:650px;
}
.goods img{
    width:100%;
    float:top;
    margin-top:0;
    position: relative;
}
.back{
    position: absolute;
    top:3px; 
    right:10px;
    border: none;
    
}
.back button{
    border:none;
    border-radius: 17.5px;
    position: absolute;
    top:3px;
    left:-320px;
    width:35px;
    height:35px;
    color:#fff;
    opacity: 0.6;   
    background-color: rgb(26, 27, 29);
}
.goods {
    background-color: #fff;
    height:500px;
    width: 100%;
}
.dol{
    color:rgb(240, 118, 4);
    font-size: 28px;
    position: absolute;
    margin-top:20px;
    left:10px;
}
.text{
    font-weight: 550;
    font-size: 16px;
    position: absolute;
    margin-top:60px;
    color: rgb(46, 46, 46);
}
.bottom{
    width:380px;
    position: fixed;
    bottom:0;
    height:60px;
    padding-bottom: 0;
    background-color: #fff;
}
.left{
    float:left;
    margin-left:13px;
    margin-top: 5px;
    text-align: center;
}
.left p{
    font-size: 11px;
    color:rgb(165, 166, 167);
    margin-left: 3px;
    margin-right: 12px;
}
.left p span {
    padding-left: 10px;
    padding-right: 5px;
}
.right{
    border-radius: 20px;
    width:230px;
    height:40px;
    float: left;
    right:20px;
    margin-top:10px;
    line-height: 40px;
    background: -webkit-linear-gradient(left,rgb(243, 188, 9),rgb(252, 117, 38)); 
}
#che{
    width:140px;
    height:40px;
    font-size: 16px;
    color: #fff;
    margin-right: 35px;
    margin-left: 30px;
    text-align: center;
    font-weight: 550;

}
#buy{
    width:140px;
    height:40px;
    font-size: 16px;
    color: #fff;
    margin-right: 10px;
    font-weight: 550;
}